import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import Animator from './components/animator'
import Main from './components/Main.vue'

Vue.config.productionTip = false

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    { path: '/', component: Main }, // all paths are defined without the hash.
    {
      path: '/animator',
      component: Animator.Animator,
      children: [
        {
          path: '/',
          component: Animator.Guides,
          children: [
            {
              path: '/',
              component: Animator.GuidesIntro
            },
          ]
        },
        {
          path: 'guides',
          component: Animator.Guides,
          children: [
            {
              path: '/',
              component: Animator.GuidesIntro
            },
            {
              path: 'intro',
              component: Animator.GuidesIntro
            },
            {
              path: 'start',
              component: Animator.GuidesStart
            },
            {
              path: 'basic',
              component: Animator.GuidesBasic
            },
            {
              path: 'multi',
              component: Animator.GuidesMulti
            },
            {
              path: 'attention',
              component: Animator.GuidesAttention
            },
            {
              path: 'entrance',
              component: Animator.GuidesEntrance
            },
            {
              path: 'exit',
              component: Animator.GuidesExit
            },
            {
              path: 'hotkey',
              component: Animator.GuidesHotkey
            },
            {
              path: 'event',
              component: Animator.GuidesEvent
            },
            {
              path: 'scene',
              component: Animator.GuidesScene
            }
          ]
        },
        {
          path: 'demo',
          component: Animator.Demo,
          children: [
            {
              path: '/',
              component: Animator.DemoAttention
            },
            {
              path: 'attention',
              component: Animator.DemoAttention
            },
            {
              path: 'entrance',
              component: Animator.DemoEntrance
            },
            {
              path: 'exit',
              component: Animator.DemoExit
            }
          ]
        },
        {
          path: 'policy',
          component: Animator.Policy
        },
        {
          path: 'terms',
          component: Animator.Terms
        },
        {
          path: 'contact',
          component: Animator.Contact
        }
      ]

    },
  ]
})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
